        <div>
          <button @click="toggleForm" class="add-button">добавить персонажа</button>
          <div v-if="showForm" class="modal">
            <form v-on:submit.prevent="addPoint">
              <div class="character-form">
                <label for="genshinCharacter">Выберите имя персонажа:</label>
                <select v-model="newCharacter.name" id="genshinCharacter">
                  <option v-for="character in genshinCharacters" :key="character.id" :value="character.name">
                    {{ character.name }}
                  </option>
                </select>
                <div v-if="newCharacter.name">
                  <div>
                    <div class="level-control">
                      <label for="firstLevelRange">Уровень 1:</label>
                      <input type="range" min="1" max="90" v-model="newCharacter.firstLevel" id="firstLevelRange" class="level-range">
                      <span>{{ newCharacter.firstLevel }}</span>
                    </div>
                  </div>
                  <div>
                    <label for="secondLevelRange">Уровень 2:</label>
                    <input type="range" min="1" max="90" v-model="newCharacter.secondLevel" id="secondLevelRange" class="level-range">
                    <span>{{ newCharacter.secondLevel }}</span>
                  </div>
                  <div>
                    <label for="thirdLevelRange">Уровень 3:</label>
                    <input type="range" min="1" max="90" v-model="newCharacter.thirdLevel" id="thirdLevelRange" class="level-range">
                    <span>{{ newCharacter.thirdLevel }}</span>
                  </div>
                  <button type="submit" @click="addCharacter">Добавить</button>
                </div>
              </div>
            </form>
        </div>
      </div>
      <table v-if="characters.length > 0">
        <tbody class="pers">
        <tr v-for="(character, index) in characters" :key="index" :class="{ 'favorite': character.favorite }">
          <td>
          <img :src="character.imagePath" :alt="character.name" style="width: 40%; height: 40%;">
          </td>
          <td>
            <tr>
              <span class="character-name">{{ character.name }}</span>
              <button class="favorite-button" @click="toggleFavorite(character)">
                ❤️
              </button>
            </tr>
            <tr>
              <div class="level-up-days">
                <p>Дни повышения уровня: {{ character.result }}</p>
              </div>
            </tr>
          </td>
          <td class="levels">
          <tr>
            <p class="character-stat">Нормальная атака: {{ character.firstLevel }}</p>
            <div class="level-control">
              <button @click="decreaseLevel(character, 'firstLevel')" class="level-button_1"><</button>
            <div class="level-bar-container">
              <div class="level-bar_1" :style="{ width: (character.firstLevel * 1.11) + '%' }"></div>
            </div>
              <button @click="increaseLevel(character, 'firstLevel')" class="level-button_1">></button>
            </div>
          </tr>
          <tr>
            <p class="character-stat">Элементальный навык: {{ character.secondLevel }}</p>
            <div class="level-control">
              <button @click="decreaseLevel(character, 'secondLevel')" class="level-button_2"><</button>
              <div class="level-bar-container">
                <div class="level-bar_2" :style="{ width: (character.secondLevel * 1.11) + '%' }"></div>
              </div>
              <button @click="increaseLevel(character, 'secondLevel')" class="level-button_2">></button>
            </div>
          </tr>
          <tr>
            <p class="character-stat">Взрыв стихий: {{ character.thirdLevel }}</p>
            <div class="level-control">
              <button @click="decreaseLevel(character, 'thirdLevel')" class="level-button_3"><</button>
              <div class="level-bar-container">
                <div class="level-bar_3" :style="{ width: (character.thirdLevel * 1.11) + '%' }"></div>
              </div>
              <button @click="increaseLevel(character, 'thirdLevel')" class="level-button_3">></button>
            </div>
          </tr>
          </td>
        </tr>
        </tbody>
      </table>
    </div>

    </div>
    <div id="app_1">
      <footer class="link">
        <a class="link" href="/" v-on:click.prevent="logoutRequest"><img  src="./public/icon.png" alt="About Page"></a>
      </footer>
    </div>
  </div>
</template>

<script>
export default {
  name: "Main",
  data() {
    return {
      action: 'main',
      selectedCharacter: '',
      name: '',
      firstLevel: 1,
      secondLevel: 1,
      thirdLevel: 1,
      newCharacter: {
        name: 'F',
        firstLevel: 1,
        secondLevel: 1,
        thirdLevel: 1,
        imagePath: 'https://i.pinimg.com/originals/cb/6b/57/cb6b57826dc61b9f419ad29ddceb9b2f.jpg'
      },
      characters: [],
      showForm: false,
      genshinCharacters: [
        { id: 1, name: 'Amber', imagePath:'https://w0.peakpx.com/wallpaper/1/448/HD-wallpaper-video-game-genshin-impact-amber-genshin-impact.jpg', levelUpDays: 'ПН ВТ ВС'},
        { id: 2, name: 'Barbara', imagePath:'https://w.forfun.com/fetch/2d/2d808e12eff0a821ac8fb1c7b70d5ccf.jpeg'},
        { id: 3, name: 'Beidou', imagePath:'https://images8.alphacoders.com/110/1109824.png'},
        { id: 4, name: 'Bennett', imagePath:'https://w.forfun.com/fetch/d3/d33f333e466fc048944d056a56bff2af.jpeg'},
        { id: 5, name: 'Chongyun', imagePath:'https://cdn.mos.cms.futurecdn.net/RcA6MYmVUUdJSbLzUpf5XY.png'},
        { id: 6, name: 'Diluc', imagePath:'https://w.forfun.com/fetch/9a/9a11699fe9b884606f08f59abcdc08d1.jpeg'},
        { id: 7, name: 'Diona', imagePath:'https://volsiz.ru/wp-content/uploads/2022/08/weacrmayfv3dlhkyzukdab.jpg'},
        { id: 8, name: 'Eula', imagePath:'https://volsiz.ru/wp-content/uploads/2022/08/zvxz5hwytxwm6m8ogqwthc.jpg'},
        { id: 9, name: 'Fischl', imagePath:'https://rerollcdn.com/GENSHIN/Characters/1/Fischl.png'},
        { id: 10, name: 'Ganyu', imagePath:'https://i1.wp.com/traveler.gg/wp-content/uploads/Ganyu-Portrait.png?fit=2154%2C1320&ssl=1'},
        { id: 11, name: 'Hu Tao', imagePath:'https://w.forfun.com/fetch/30/300baf40bdb2bfe313364a07ca32dad0.jpeg'},
        { id: 12, name: 'Jean', imagePath:'https://imagesvc.meredithcorp.io/v3/mm/image?url=https://www.pockettactics.com/wp-content/uploads/2020/09/genshin-impact-tier-list-2.jpg'},
        { id: 13, name: 'Kaeya', imagePath:'https://i.pinimg.com/originals/26/5b/4c/265b4c3cd73695ee17f8a5d91c86ee3a.jpg'},
        { id: 14, name: 'Klee', imagePath:'https://i.pinimg.com/originals/da/9a/30/da9a30bb1ec0b4c432f835b95ee6bdd2.jpg'},
        { id: 15, name: 'Lisa', imagePath:'https://mundogenshinimpact.com/wp-content/uploads/Gesnhin-Impact-Lisa.png'},
        { id: 16, name: 'Mona', imagePath:'https://i.playground.ru/i/pix/3089906/image.jpg'},
        { id: 17, name: 'Ningguang', imagePath:'https://i.pinimg.com/originals/0e/66/ab/0e66abd9206b3c65a26503853ee68db0.png'},
        { id: 18, name: 'Noelle', imagePath:'https://wallpapercave.com/wp/wp8505426.png'},
        { id: 19, name: 'Qiqi', imagePath:'https://besthqwallpapers.com/Uploads/19-1-2021/152617/qiqi-artwork-genshin-impact-kaeya-alberich-ghosts.jpg'},
        { id: 20, name: 'Razor', imagePath:'https://avatars.mds.yandex.net/get-mpic/5234219/img_id3919635833406184053.jpeg/orig'}
      ]
    };
  },
  created() {
    this.$axios({
      method: 'get',
      url:  this.$BaseURL + 'points',
    }).then(response => {
          // Обновление данных в таблице
          this.characters = response.data;
    }).catch(error => {
          console.log(error);
    });
  },
  methods: {
    addCharacter() {
      if (this.newCharacter.name) {
        const selectedCharacter = this.genshinCharacters.find(char => char.name === this.newCharacter.name);
        if (selectedCharacter) {
          this.characters.push({
            name: this.newCharacter.name,
            firstLevel: this.newCharacter.firstLevel,
            secondLevel: this.newCharacter.secondLevel,
            thirdLevel: this.newCharacter.thirdLevel,
            imagePath: selectedCharacter.imagePath,
            levelUpDays: this.newCharacter.levelUpDays,
          });
          console.log(this.newCharacter.name, this.newCharacter.firstLevel, this.newCharacter.secondLevel, this.newCharacter.thirdLevel);
          this.addPoint();
          this.resetForm();
          this.toggleForm(); // Скрыть форму после добавления
        }
      }
    },
    resetForm() {
      this.newCharacter = {
        name: '',
        firstLevel: 1,
        secondLevel: 1,
        thirdLevel: 1,
        levelUpDays: ''
      };
    },
    toggleForm() {
      this.showForm = !this.showForm; // Показать/скрыть форму
    },
    getCharacterImage(characterName) {
      // Используем путь к изображению, хранимый в genshinCharacters
      const selectedCharacter = this.genshinCharacters.find(char => char.name === characterName);
      if (selectedCharacter) {
        return selectedCharacter.imagePath;
      }
      // Вернуть путь к заглушке, если путь к изображению не найден
      return '/placeholder-image.jpg';
    },
    increaseLevel(character, levelType) {
      if (character[levelType] < 90) {
        character[levelType]++;
        this.addPointRequest(character.name, character.firstLevel, character.secondLevel, character.thirdLevel, character.levelUpDays, character.imagePath)
      }
    },
    decreaseLevel(character, levelType) {
      if (character[levelType] > 1) {
        character[levelType]--;
        // this.updateCharacterOnServer(character); // Вызываем метод обновления данных на сервере
        this.addPointRequest(character.name, character.firstLevel, character.secondLevel, character.thirdLevel, character.levelUpDays, character.imagePath)
      }
    },
    toggleFavorite(character) {
      character.favorite = !character.favorite;

      if (character.favorite) {
        // Если персонаж добавлен в избранное, переместим его в начало массива
        const index = this.characters.indexOf(character);
        if (index !== -1) {
          this.characters.splice(index, 1);
          this.characters.unshift(character);
        }
      } else {
        // Если персонаж удален из избранного, переместим его обратно в изначальное положение в массиве
        const index = this.characters.indexOf(character);
        if (index !== -1) {
          this.characters.splice(index, 1);
          this.characters.push(character);
        }
      }
    },
    logoutRequest: function () {
      this.$axios({
        method: 'post',
        url: this.$BaseURL + 'logout',
      }).then(response => {
        this.response = response;
        localStorage.removeItem('currentUser');
        this.$router.push('/');
      }).catch(error => {
        console.log(error);
      });
    },
    addPointRequest: function (name, firstLevel, secondLevel, thirdLevel, levelUpDays, imagePath) {
      let base64Credential = localStorage.getItem('currentUser');
      let username = localStorage.getItem('username');
      console.log(name, firstLevel, secondLevel, thirdLevel);
      return this.$axios({
        method: 'post',
        url: this.$BaseURL + 'points',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': base64Credential
        },
        data: {name: name, firstLevel: firstLevel, secondLevel: secondLevel, thirdLevel: thirdLevel, levelUpDays: levelUpDays, imagePath: imagePath }
      }).then(response => {

        // console.log(response);
        let result = response.data.result;
        const existingCharacterIndex = this.characters.findIndex(char => char.name === name);
        const selectedCharacter = this.genshinCharacters.find(char => char.name === name); // Находим соответствующий объект-персонаж
        if (existingCharacterIndex !== -1) {
          // Update existing character if found
          this.$set(this.characters, existingCharacterIndex, {name, firstLevel, secondLevel, thirdLevel, result, imagePath: selectedCharacter ? selectedCharacter.imagePath : 'https://i.pinimg.com/originals/cb/6b/57/cb6b57826dc61b9f419ad29ddceb9b2f.jpg' , username});
          console.log(this.characters)
          // localStorage.setItem('characters', JSON.stringify(this.characters));
        } else {
          // Add new character if not found
          // this.characters.unshift({name, firstLevel, secondLevel, thirdLevel, result});
          this.characters.unshift({name, firstLevel, secondLevel, thirdLevel, result, imagePath: selectedCharacter ? selectedCharacter.imagePath : 'https://i.pinimg.com/originals/cb/6b/57/cb6b57826dc61b9f419ad29ddceb9b2f.jpg' });
        }
        // this.characters.unshift({name: name, firstLevel: firstLevel, secondLevel: secondLevel, thirdLevel: thirdLevel, result: result});
        this.response = response;
        console.log(response);

      }).catch(error => {
        console.log(error.response.data);
        return false;
      });
    },
    addPoint: function (){
      if (this.newCharacter.name && this.newCharacter.firstLevel && this.newCharacter.secondLevel && this.newCharacter.thirdLevel) {
        this.addPointRequest(this.newCharacter.name, this.newCharacter.firstLevel, this.newCharacter.secondLevel, this.newCharacter.thirdLevel, this.newCharacter.imagePath);
        console.log(this.newCharacter.name, this.newCharacter.firstLevel, this.newCharacter.secondLevel, this.newCharacter.thirdLevel)}
      else{
        console.log('Некоторые или все данные не заполнены');
        console.log(this.newCharacter.name, this.newCharacter.firstLevel, this.newCharacter.secondLevel, this.newCharacter.thirdLevel);
      }
    },
  }
}
</script>

